@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
@import url(https://unpkg.com/tailwindcss@^2/dist/tailwind.min.css);
.u-button {
  font-size: 1rem;
  color: var(--color-white);
  padding: var(--s1) var(--s4);
  background: var(--color-turqouise-light);
  border-radius: 0.25rem;
  border: none;
}

.button {
  font-size: 2.25rem;
  color: var(--color-white);
  padding: 10px 10px;
  min-width: 20%;
  display: inline-block;
  background: var(--color-turqouise-light);
  border-radius: 0.25rem;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
}

.game-board-player__active {
  background: var(--color-blue-light);
  color: var(--color-white);
}

.game-board-img {
  height: 10rem;
  text-align: center;
  display: block;
  margin: auto;
  border-radius: 0.25rem;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.progress-bar {
  width: 100%;
  position: relative;
  margin-bottom: var(--s5);
}

.progress-bar__status {
  position: absolute;
  left: 45%;
}

.progress-bar__bar {
  height: 0.5rem;
  background-color: var(--color-blue-light);
  transition: width 200ms ease-in-out;
}

.menu {
  max-width: 22rem;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2rem;
}

.menu__item {
  border: 1px solid var(--color-blue-light);
  border-radius: 0.5rem;
  padding: 1rem;
  display: block;
  text-decoration: none;
  color: var(--color-turqouise-light);
}

.menu__item:hover,
.menu__item:active {
  text-decoration: none;
}

:root {
  --color-turqouise-light: #00a99e;
  --color-turqouise-dark: #177a8c;
  --color-blue-light: #20537b;
  --color-blue-dark: #262a6a;
  --color-white: #ffffff;

  --s1: 0.25rem;
  --s4: 1rem;
  --s5: 1.25rem;
}

body {
  font-family: "Noto Sans", sans-serif;
  background: #ffffff;
  background: var(--color-white);

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

